.paginator {
    margin-top: 30px;
    display: block;
}

.pageNumber {
    padding: 2px;
    border: 1px solid gray;
    font-size: 14px;
    cursor: pointer;
}

.selectedPage {
    font-weight: bold;
    padding: 5px;
    cursor: pointer;
}

.next {
    margin-left: 5px;
}

.prev {
    margin-right: 5px;
}