@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
body {
    margin: 0;
    font-family:
            /*-apple-system, BlinkMacSystemFont, 'Segoe UI', */
    'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background: #dddddd;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app-wrapper {
    display: grid;
    width: 100%;
    margin: 0 auto;

    grid-template-areas: "header header" "backImg backImg" "content content";

    grid-template-rows: 60px 150px 1fr;
    grid-template-columns: 2fr 10fr;
    /*grid-gap: 10px;*/
}

.app-wrapper-content {
    grid-area: content;
    background-color: #ddd;
}

.backHeader {
    grid-area: header;
    background-color: #007c33;
}

.backImg {
    grid-area: backImg;
    background: url(/SocialNetwork/static/media/backImg.ca6cbcf5.jpg);
    background-position: 0px -67px;
    background-size: cover;
}

.contentBlock {
    display: grid;
    grid-template-areas: "nav content";
    grid-template-columns: 15% 85%;
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
}

.blockInsideContent {
    background: white;
    padding: 20px;
    margin: 10px;
    margin-top: -50px;
    box-shadow: 2px 2px 5px 0 #00000059;
}

button {
    background-color: #2f9c30;
    border: none;
    color: white;
    padding: 5px 15px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 13px;
    border-radius: 2px;
    cursor: pointer;
    text-transform: uppercase;
    font-family: "Roboto";
    -webkit-transition: all 0.1s linear;
    transition: all 0.1s linear;
}

button:hover {
    background-color: #237524;
}




